* {
  box-sizing: border-box;
}

body {
  background-color: #e0e0e0;
  font-family: monospace;
  font-size: 16px;
}

.grid {
  display: grid;
  grid-gap: 20px;
  gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
}

