}

/*-----------------------------------------------------------------------------------------*/

.App {
  text-align: center;
}

.App-link {
  color: #61dafb;
}

/* ------------------ Grid Galerie ---------------------- */

.b-gal-grid .b-gal-w {
	display: grid;
	grid-column-gap: 20px;
	-webkit-column-gap: 20px;
	        column-gap: 20px;
	grid-row-gap: 20px;
	row-gap: 20px;
	padding: 20px;

	grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
}

.b-gal-grid.grid-2 .b-gal-w {
	@media screen and (min-width: 400px) {
		grid-template-columns: repeat(2, 1fr);
	}
}


.b-gal-grid.grid-3 .b-gal-w {
	@media screen and (min-width: 600px) {
		grid-template-columns: repeat(3, 1fr);
	}
}

.b-gal-grid.grid-4 .b-gal-w {
	@media screen and (min-width: 960px) {
		grid-template-columns: repeat(4, 1fr);
	}
}

.b-gal-grid.grid-5 .b-gal-w {
	@media screen and (min-width: 960px) {
		grid-template-columns: repeat(5, 1fr);
	}
}

.Card {
    background: white;
    border-radius: 10px;
}

.Card:hover {
    background: #f5f5f5;
}

.Card__img {
    text-align: center;
}

.Card__types {
    display: flex;
    justify-content: center;
}

.Card__type {
    padding: 5px 10px;
    margin: 10px 10px 10px 0;
    border-radius: 5px;
    background: cornflowerblue;
    color: #fff;
}

.Card__name {
    text-align: center;
    text-transform: uppercase;
    font-weight: 800;
}

.Card__info {
    margin-left: 10px;
    text-align: center;
}

.Card__data {
    padding-bottom: 10px;
}
.grid-container {
  width: 70%;
  display: grid;
  margin: 0 auto;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 10px;
}

.btn {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.btn button {
  border-style: none;
  padding: 5px 10px;
  margin-right: 10px;
  border-radius: 5px;
  background: cornflowerblue;
  color: #fff;
  font-size: 1em;
}
/* Styly Galerie Feed ------------------ */

.b-gal-feed {
    padding: 20px;
}

.b-gal-feed .b-gal-w {
    width: 100%;
    padding: 0;
    margin: 0;
}

.b-gal-feed .b-gal-item {
    width: calc(100% / 2 - 1rem);
    margin: 0.5rem;
    border-radius: 5px;
}

@media (min-width: 700px) {
    .b-gal-feed .b-gal-item {
        width: calc(100% / 3 - 1rem);
        /*margin: 0.5rem;*/
    }
}

@media (min-width: 1200px) {
    .b-gal-feed .b-gal-item {
        width: calc(100% / 4 - 1rem);
        /*margin: 0.5rem;*/
    }
}

.b-gal-feed picture {
    position: relative !important;
}

.b-gal-feed .b-gal-item img {
    border-radius: 5px;
}
/* Společné styly pro galerie -------------------------- */

.b-gal {
    width: 70%;
    margin: 0 auto;
}

.b-gal-w {
    padding: 20px;
}

.b-gal .b-gal-item {
    position: relative;
    overflow: hidden;
}

.b-gal-a {
    line-height: 0;
    text-decoration: none;
    position: relative;
    overflow: hidden;
    display: block;
}

.b-gal picture {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
}

.b-gal img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: 50% 50%;
}

.b-gal-t {
    width: 100%;
}

.b-gal .b-gal-t span {
    display: block;
    text-align: center;
}

.b-gal .b-gal-t span span {
    padding-top: 10px;
}

/* Styly Galerie Classic ----------------------------- */

.b-gal-classic .b-gal-a {
    padding-bottom: 100%;
}
.b-gal-horizontal {
  padding: 0;
}

.b-gal-horizontal .b-gal-w {
  display: flex;
  flex-flow: row wrap;
  grid-gap: initial;
  gap: initial;
  padding: initial;
}

.b-gal-horizontal .b-gal-w::after {
  display: block;
  flex-grow: 99;
  min-width: 300px;
  height: 0;
  content: '';
}

.b-gal-horizontal .b-gal-item {
  position: relative;
  flex: 1 1 content;
  width: auto;
  height: auto;
  margin: 10px;
  display: block;
}

.b-gal-horizontal .b-gal-item .b-gal-a {
  position: relative;
  display: block;
  overflow: visible;
}

.b-gal-horizontal picture {
  position: relative;
}

.b-gal-horizontal img {
  width: 100%;
  height: 100%;
}

.b-gal-horizontal .b-gal-img {
  height: 100%;
  overflow: hidden;
  transition: all .3s ease;
}

.b-gal-horizontal .b-gal-t.title-position {
  position: absolute;
  top: auto;
  right: 0;
  bottom: auto;
  left: 0;
}

@supports (-webkit-marquee-repetition: infinite) and (object-fit: fill) {
  .b-gal-horizontal .b-gal-img {
    margin: 0 -.3px;
  }
}

.safari .b-gal-horizontal .b-gal-img {
  margin: 0 -.3px;
}

/* Media queries --------------------------------*/

@media (min-width: 360px) {
  .b-gal-horizontal {
    padding: 2px;
  }

  .b-gal-horizontal .b-gal-item {
    margin: 8px;
  }

  .b-gal-horizontal .b-gal-item:last-child {
    flex: 0 1 content;
  }

  .b-gal-horizontal img {
    height: 115px;
  }
}

@media (min-width: 600px) {
  .b-gal-horizontal {
    padding: 0;
  }

  .pr-0 .b-gal-horizontal .b-gal-w {
    padding: 5px;
  }

  .b-gal-horizontal .b-gal-item {
    margin: 10px;
  }

  .b-gal-horizontal img {
    height: 140px;
  }
}

@media (min-width: 900px) {
  .b-gal-horizontal {
    padding: 3px;
  }

  .b-gal-horizontal .b-gal-item {
    margin: 12px;
  }

  .b-gal-horizontal img {
    height: 200px;
  }
}

@media (min-width: 1200px) {
  .b-gal-horizontal {
    padding: 5px;
  }

  .b-gal-horizontal .b-gal-item {
    margin: 15px;
  }
}

